import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
  {
    path: "/login",
    alias: "/",
    component: () => import("../views/login"),
    meta: {
      notAuth: true,
    },
  },
  {
    path: "/carteleras",
    component: () => import("../views/Carteleras"),
    meta: {
      requiredAuth: true,
    },
  },
  {
    path: "/promociones",
    component: () => import("../views/Promociones"),
    meta: {
      requiredAuth: true,
    },
  },
  {
    path: "/proximamente",
    component: () => import("../views/Proximamente"),
    meta: {
      requiredAuth: true,
    },
  },
  {
    path: "/tandas",
    component: () => import("../views/Tandas"),
    meta: {
      requiredAuth: true,
    },
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

router.beforeEach((to, from, next) => {
  let session = router.app.$cookies;

  if (to.matched.some((route) => route.meta.requiredAuth)) {
    if (!session.isKey("token")) {
      next("/login");
      return;
    }
  } else if (to.matched.some((route) => route.meta.notAuth)) {
    if (session.isKey("token")) {
      next("/carteleras");
      return;
    }
  }

  next();
  return;
});

export default router;
